import { reactive, watch } from 'vue'
export default function () {
  const showdata = reactive({
    click_num: 0,
    showflag: true,
  })

  watch(()=>showdata.click_num, (newSum) => {
    if (newSum % 2 == 0) {
      showdata.showflag = true
    } else {
      showdata.showflag = false
    }
  })

  return { showdata }
}
