import { inject } from 'vue'
import UserStore from '../../store/UserStore.ts'
import { Buffer } from 'buffer'
import JumpTips from '../../store/JumpTips.ts'
import Encrypt from '../../components/AES/index.js'
import { useRouter } from "vue-router"

export default function (showdata, UserInput) {
  const axios = inject('$axios')
  const use_UserStore = UserStore()
  const use_JumpTips = JumpTips()
  const encrypt = Encrypt()
  const router = useRouter()
  /** 
   * 1. UUID
   * 2.
   */
  !use_UserStore.UUID && use_UserStore.CreateUUID()

  async function get_captcha(e) {
    let resultValue = await axios.get(`/image_codes/${use_UserStore.UUID}`, {
      responseType: 'arraybuffer',
    })
    e.target.nextElementSibling.style.display = 'block'
    e.target.nextElementSibling.src = 'data:image/png;base64,' + Buffer.from(resultValue).toString('base64')
  }

  //[]
  async function getEmail_captcha(e) {
    if (!UserInput.value.Prepping[2]) return
    e.target.style.pointerEvents = 'none'
    use_JumpTips.JumpTipsShow({ errmsg: ', windows~' }, false,{ AnimationTop: 2 + 'vh' })
    //
    let TimeOutID = setTimeout(() => {
      e.target.style.pointerEvents = 'unset'
      clearTimeout(TimeOutID)
    }, 60000)
    //
    let enEmail = encrypt.Encode(UserInput.value.Email, use_UserStore.UUID)
    axios.post(`/email_codes/${use_UserStore.UUID}`, { UserEmail: enEmail })
  }

  //
  async function send_axios(e) {
    e.target.style.pointerEvents = 'none'
    //
    let TimeOutID = setTimeout(() => {
      e.target.style.pointerEvents = 'unset'
      clearTimeout(TimeOutID)
    }, 10000)
    let type = showdata.showflag ? 'login' : 'registered'
    if (!map.get(type)(UserInput.value)) return
    //,  axios 
    use_JumpTips.JumpTipsShow({ errmsg: ', ~' }, false,{ AnimationTop: 2 + 'vh' })
    if (type === 'login') {
      let UserInputValue = encrypt.Encode(JSON.stringify(UserInput.value), use_UserStore.UUID)
      let resultValue = await axios.get(`/users/${use_UserStore.UUID}`, {
        params: {
          data: UserInputValue,
        },
      })
      if(resultValue.data?.login){
        use_JumpTips.JumpTipsShow({ errmsg: ', ' }, false,{ AnimationTop: 2 + 'vh' })
        // token  refresh  pinia
        use_UserStore.SetDefaultToken(resultValue.data.token, resultValue.data.refresh)
        //
        router.replace(`/`)
      } 
    } else {
      let UserInputValue = encrypt.Encode(JSON.stringify(UserInput.value), use_UserStore.UUID)
      let resultValue = await axios.post(`/users/${use_UserStore.UUID}`, UserInputValue)
      resultValue.data?.registered && use_JumpTips.JumpTipsShow({ errmsg: ', ' }, false,{ AnimationTop: 2 + 'vh' })
    }
  }
  let map = new Map([
    //
    [
      'login',
      (data) => {
        data.Prepping.splice(2, 1)
        return data.Prepping.every((item) => item)
      },
    ],
    //
    [
      'registered',
      (data) => {
        return data.Prepping.every((item) => item)
      },
    ],
  ])
  return { get_captcha, getEmail_captcha, send_axios }
}
