import { onMounted } from "vue"

export default function(Card,ReData){
  // Element
let CardElement = null


function FlipAnimation(){
  //, 
  ReData()
  CardElement.classList.remove('animate__flip')
  setTimeout(()=>{
    CardElement.classList.add('animate__flip')
  })
}

onMounted(()=>{
  CardElement = Card.value
})

return {FlipAnimation}

}