import CryptoJS from 'crypto-js'
//  - /
/** 
 * 1.const encrypt = Encrypt()
 * 2.encrypt.Encode(word, uuid)
 */
export default function () {
  class AES_Encode {

    CreateAesKey(uuid,date) {
      let today = date.getDay()
      let hour = date.getHours()

      let UUDI_Key = uuid.replace(/[^A-z]+/g, '').split('')
      let iv = uuid.replace(/[^0-9]+/g, '').split('')
      UUDI_Key.splice(Math.abs(today - 2), today, iv[Math.floor(hour / 3) || 2])
      UUDI_Key = UUDI_Key.join('').substring(0, 16)
      return UUDI_Key
    }

    AddString_To16(word){
      while(word.length % 16 != 0){
        word += ' '
      }
      return word
    }

    Encode(word, uuid) {
      let date = new Date()
      // key  iv
      let key = this.CreateAesKey(uuid,date)
      let iv = CryptoJS.MD5(key).toString().substring(0, 16)
      console.log('AESkey'+key+''+iv)
      key = CryptoJS.enc.Utf8.parse(this.AddString_To16(key))
      iv = CryptoJS.enc.Utf8.parse(iv)
      let srcs = CryptoJS.enc.Utf8.parse(word)
      // CBCPKCS5PaddingPKCS7
      let encrypted = CryptoJS.AES.encrypt(srcs, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7,
      })
      // Base64  axios
      return CryptoJS.enc.Base64.stringify(encrypted.ciphertext)
    }
  }

 return new AES_Encode()
}
